import Exif from 'exif-js';

const uploadFix = {
  rotateImg(img, direction, canvas) {
    // 4
    const minStep = 0;
    const maxStep = 3;
    if (img == null) return;
    // imgimg
    const height = img.height;
    const width = img.width;
    let step = 2;
    if (step == null) {
      step = minStep;
    }
    if (direction === 'right') {
      step++;
      // 
      step > maxStep && (step = minStep);
    } else if (direction === 'right2') {
      step = 2;
    } else {
      step--;
      step < minStep && (step = maxStep);
    }
    // 
    const degree = step * 90 * Math.PI / 180;
    const ctx = canvas.getContext('2d');
    switch (step) {
      case 0:
        canvas.width = width;
        canvas.height = height;
        ctx.drawImage(img, 0, 0);
        break;
      case 1:
        canvas.width = height;
        canvas.height = width;
        ctx.rotate(degree);
        ctx.drawImage(img, 0, -height);
        break;
      case 2:
        canvas.width = width;
        canvas.height = height;
        ctx.rotate(degree);
        ctx.drawImage(img, -width, -height);
        break;
      case 3:
        canvas.width = height;
        canvas.height = width;
        ctx.rotate(degree);
        ctx.drawImage(img, -width, 0);
        break;
      default:
        break;
    }
  },
  compress(img, orientation) {
    const canvas = document.createElement('canvas');
    const ctx = canvas.getContext('2d');
    // canvas
    const tCanvas = document.createElement('canvas');
    const tctx = tCanvas.getContext('2d');
    let width = img.width;
    let height = img.height;
    // 400
    let ratio = width * height / 4000000;
    if (ratio > 1) {
      console.log('400');
      ratio = Math.sqrt(ratio);
      width /= ratio;
      height /= ratio;
    } else {
      ratio = 1;
    }
    canvas.width = width;
    canvas.height = height;
    // 
    ctx.fillStyle = '#fff';
    ctx.fillRect(0, 0, canvas.width, canvas.height);
    // 100
    let count = width * height / 1000000;
    if (count > 1) {
      console.log('100W');
      // 
      count = ~~(Math.sqrt(count) + 1);
      // 
      const nw = ~~(width / count);
      const nh = ~~(height / count);
      tCanvas.width = nw;
      tCanvas.height = nh;
      for (let i = 0; i < count; i++) {
        for (let j = 0; j < count; j++) {
          tctx.drawImage(
            img,
            i * nw * ratio,
            j * nh * ratio,
            nw * ratio,
            nh * ratio,
            0,
            0,
            nw,
            nh,
          );
          ctx.drawImage(tCanvas, i * nw, j * nh, nw, nh);
        }
      }
    } else {
      ctx.drawImage(img, 0, 0, width, height);
    }
    // ios 
    if (orientation !== '' && orientation !== 1) {
      switch (orientation) {
        // 90
        case 6:
          this.rotateImg(img, 'left', canvas);
          break;
        // 90
        case 8:
          this.rotateImg(img, 'right', canvas);
          break;
        // 180
        case 3:
          this.rotateImg(img, 'right2', canvas);
          break;
        default:
          break;
      }
    }
    // 
    const ndata = canvas.toDataURL('image/jpeg', 0.6);
    tCanvas.width = tCanvas.height = canvas.width = canvas.height = 0;
    return ndata;
  },
};

export function dataURLtoFile(dataurl, filename) {
  const arr = dataurl.split(',');
  const mime = arr[0].match(/:(.*?);/)[1];
  const bstr = atob(arr[1]);
  let n = bstr.length;
  const u8arr = new Uint8Array(n);
  while (n--) {
    u8arr[n] = bstr.charCodeAt(n);
  }
  let res = null;
  try{
    res = new File([u8arr], filename, { type: mime })
  } catch(e) {
    res= new Blob([u8arr], { type: mime })
    res.lastModifiedDate =new Date();
    res.name = filename;
  }
  return res;
}

export function getOrientation(file) {
  return new Promise((resolve) => {
    // 
    Exif.getData(file, function getDataCallBack() {
      const orient = Exif.getTag(this, 'Orientation');
      resolve(orient);
    });
  });
}

export function getBase64(img, callback) {
  const reader = new FileReader();
  reader.addEventListener('load', () => callback(reader.result));
  reader.readAsDataURL(img);
}

export default uploadFix;
