
import React from 'react';
import styles from './index.module.css';
import { Upload, Icon, message, Button } from 'antd';
import { getPhotoUrl } from '../../utils/utils';
import uploadFix, { dataURLtoFile, getOrientation, getBase64 } from '../../utils/uploadFix';



function beforeUpload(file) {
  const isNotImg = file.type !== 'image/jpeg' && file.type !== 'image/png';
  if (isNotImg) {
    message.error('JPG/PNG!');
    return false;
  }

  const isLt2M = file.size / 1024 / 1024 < 2;
  if (!isLt2M) {
    message.error('2MB!');
    return false;
  }
  return true;
}

class Avatar extends React.Component {
  constructor(props) {
    super(props);

    const value = props.value || {};

    this.state = {
      photo_url: value.photo_url,
      file: value.file
    };
  }

  customRequest = obj => {
    const file = obj.file;
    // 
    getOrientation(file).then((orientation) => {
      const reader = new FileReader();
      const img = new Image();
      // iosorientation1
      const needRotate = (orientation && orientation !== 1)
      reader.onload = (e) => {
        if (needRotate) {
          img.src = e.target.result;
          img.onload = () => {
            // 
            const data = uploadFix.compress(img, orientation);
            // base64tofile
            const newFile = dataURLtoFile(data, file.name);
            this.setState({
              photo_url: data,
              file: newFile
            });
            this.triggerChange({file: newFile})
          };            
        } else {
          // 
          this.setState({
            photo_url:  e.target.result,
            file,
          });
          this.triggerChange({file: file})
        }
      };
      reader.readAsDataURL(file);
    });
    return false;
  };


  triggerChange = (file)  => {
    const onChange = this.props.onChange;
    if (onChange) {
      // onChange(Object.assign({}, this.state, photo_url));
      // onChange({...this.state,file});
      onChange(Object.assign({}, this.state, file));

    }
  }

  // 
  removeImage = (e) => {
    e.stopPropagation();
    this.setState({
      photo_url: '',
      file: ''      
    }, this.props.onRemove)
    // value
    this.triggerChange({
      photo_url: '',
      file: ''
    })
  }



  render() {
    const uploadButton = (
      <div>
        <Icon type={this.state.loading ? 'loading' : 'plus'} />
        <div className="ant-upload-text">Upload</div>
      </div>
    );
    const { photo_url } = this.state;
    const { deletable } = this.props;
    let url = photo_url || ''

    if (url) {
      if (url.indexOf('data:image/') !== 0 && photo_url.indexOf('http') !== 0) {
        url = getPhotoUrl({photo_url: url})
        url += `?_=` + Date.now()
      }
    }

    return (
      <Upload
        name="avatar"
        accept="image/jpg, image/jpeg, image/png"
        listType="picture-card"
        className="avatar-uploader"
        showUploadList={false}
        action=""
        beforeUpload={beforeUpload}
        customRequest={this.customRequest}
      >
      <span className={styles.imgWrapper}>
        {url ? <img src={url} alt="" className={styles.img} /> : uploadButton}
        {url && deletable ? <Button type="default" shape="circle" icon="delete" size='small' className={styles.btnDel} onClick={this.removeImage}/> : null}
        </span>
      </Upload>
    );
  }
}

Avatar.defaultProps = {
  onRemove: () => {}
}


export default Avatar;
