import React from 'react';
import {
  Row,
  Col,
  Card,
  Form,
  Input,
  Button,
  message,
  Divider,
  Popconfirm,
  Menu,
  Dropdown,
  Icon,
  Select,
  Modal,
  Cascader,
  Badge,
} from 'antd';
import { injectIntl, FormattedMessage, defineMessages } from 'react-intl';

import StandardTable from '../../../components/StandardTable';
import PhotoBox from '../../../components/PhotoBox'
import ImageViewer from '../../../components/ImageViewer'
import ListHeader from '../../../components/ListHeader'
import SelectAccount from '../../../components/SelectAccount'

import VisitorList from './VisitorList'
// import EquipmentList from './EquipmentList'
import BatchForm from '../../../components/BatchAddPeople'
import EditForm from './EditForm'
import BindEquipment from './BindEquipment'
import ViewForm from './ViewForm'


import {
  getPeoples,
  getPeopleInfo,
  addPeople,
  updatePeople,
  deletePeople,
  batchDeletePeople,
  batchAddPeople,
  resetPwd,
  approval,
  getVisitorList,
} from '../../../api/personnel';

import { defaultPageCount, fixUndefinedData, parseDate, dateTimeToMoment } from '../../../utils/utils';
import { cancelApiRequsets } from '../../../utils/fetch';
import { loadState } from '../../../utils/localStorage';
import maxLength from '../../../utils/maxLength'
import groupBy from 'lodash/groupBy'

import styles from './index.module.scss';

const FormItem = Form.Item;
const Option = Select.Option;

const messages = defineMessages({
  personnelName: {
    id: 'personnel.name',
    defaultMessage: '',
  },
  photo: {
    id: 'personnel.photo',
    defaultMessage: '',
  },
  mobilePhone: {
    id: 'personnel.mobilePhone',
    defaultMessage: ''
  },
  identifier: {
    id: 'common.identifier',
    defaultMessage: ''
  },
  sourceAccount: {
    id: 'account.sourceAccount',
    defaultMessage: ''
  },
  status: {
    id: 'common.status',
    defaultMessage: '',
  },
  registered: {
    id:'common.registered',
    defaultMessage: ''
  },
  rejected: {
    id: 'common.rejected',
    defaultMessage: ''
  },
  approvalPending: {
    id: 'common.approvalPending',
    defaultMessage: ''
  },
  approve: {
    id: 'common.approve',
    defaultMessage: ''
  },
  reject: {
    id: 'common.reject',
    defaultMessage: ''
  },
  visitorNumber: {
    id: 'personnel.visitorNumber',
    defaultMessage: ''
  },
  operate: {
    id: 'common.operate',
    defaultMessage: ''
  },
  operateSuccess: {
    id: 'common.operate.success',
    defaultMessage: '!'
  },
  operateError: {
    id: 'common.operate.error',
    defaultMessage: '!'
  },
  relateDevice: {
    id: 'device.relateDevice',
    defaultMessage: ''
  },
  resetPassword: {
    id: 'account.resetPassword',
    defaultMessage: '',
  },
  resetConfirm: {
    id: 'common.reset.confirm',
    defaultMessage: '?'
  },
  resetSuccess: {
    id: 'common.reset.success',
    defaultMessage: '!',
  },
  resetError: {
    id: 'common.reset.error',
    defaultMessage: '!',
  },
  edit: {
    id: 'common.edit',
    defaultMessage: '',
  },
  search: {
    id: 'common.search',
    defaultMessage: '',
  },
  reset: {
    id: 'common.reset',
    defaultMessage: ''
  },
  all: {
    id: 'common.all',
    defaultMessage: ''
  },
  delete: {
    id: 'common.delete',
    defaultMessage: '',
  },
  deleteSuccess: {
    id: 'common.delete.success',
    defaultMessage: '!'
  },
  deleteError: {
    id: 'common.delete.error',
    defaultMessage: '!',
  },
  failedToGetData: {
    id: 'common.error.failedToGetData',
    defaultMessage: '!'
  },
  batchAdd: {
    id: 'common.batchAdd',
    defaultMessage: '',
  },
  batchAddSuccess: {
    id: 'common.batchAdd.success',
    defaultMessage: '!'
  },
  batchAddError: {
    id: 'common.batchAdd.error',
    defaultMessage: '!'
  },
  batchDelete: {
    id: 'common.batchDelete',
    defaultMessage: '',
  },
  deleteConfirm: {
    id: 'common.delete.confirm',
    defaultMessage: '?'
  },
  batchDeleteConfirm: {
    id: 'common.batchDelete.confirm',
    defaultMessage: '?'
  },
  batchDeleteSuccess: {
    id: 'common.batchDelete.success',
    defaultMessage: '!'
  },
  batchDeleteError: {
    id: 'common.batchDelete.error',
    defaultMessage: '!'
  },
  addPeople: {
    id: 'personnel.addPeople',
    defaultMessage: '',
  },
  updatePeople: {
    id: 'personnel.updatePeople',
    defaultMessage: '',
  },
  photoFormat: {
    id: 'personnel.photo.format',
    defaultMessage: '+JPG/JPEG/PNG2M!'
  },
  partFailError: {
    id: 'personnel.addError.partFail',
    defaultMessage: '!'
  },
  failError: {
    id:'personnel.addError.fail',
    defaultMessage: '!'
  },
  photoFormatError: {
    id: 'personnel.addError.photoFormat',
    defaultMessage: '',
  },
  add: {
    id: 'common.add',
    defaultMessage: '',
  },
  update: {
    id: 'common.update',
    defaultMessage: '',
  },
  success: {
    id: 'common.success',
    defaultMessage: '!'
  },
  failed: {
    id: 'common.failed',
    defaultMessage: '!'
  },
  peopleList: {
    id: 'personnel.peopleList',
    defaultMessage: ''
  },
  confirmDelete: {
    id: 'common.confirmDelete',
    defaultMessage: ''
  },
  cancel: {
    id: 'common.cancel',
    defaultMessage: ''
  },
  idcardNumber: {
    id: 'personnel.idcardNumber',
    defaultMessage: ''
  },
  peopleDetail: {
    id: 'personnel.details',
    defaultMessage: '',
  },
})
class PersonnelManage extends React.Component {
  columns = [
    {
      title: <FormattedMessage {...messages.personnelName}/>,
      dataIndex: 'people_name',
      render: (text, record) => (
        <React.Fragment>
          <a className={styles.wordBreak} href="javascript:;" onClick={() => this.showViewModal(record)}>{text}</a>
        </React.Fragment>
      )
    },
    {
      title: <FormattedMessage {...messages.photo}/>,
      dataIndex: 'image',
      render: (text, record) =>
        <React.Fragment>
          {record.image && <PhotoBox photo={record.thumbnail} width={50} height={50} onClick={() => {this.showImage(text)}}/>}
        </React.Fragment>
    },
    // {
    //   title: <FormattedMessage {...messages.mobilePhone}/>,
    //   dataIndex: 'telephone'
    // },
    // {
    //   title: <FormattedMessage {...messages.idcardNumber}/>,
    //   dataIndex: 'idcard_number'
    // },
    {
      title: <FormattedMessage {...messages.identifier}/>,
      dataIndex: 'people_id'
    },
    {
      title: <FormattedMessage {...messages.sourceAccount}/>,
      dataIndex: 'account_name',
      render: (text, record) => (
        <React.Fragment>
          <span className={styles.wordBreak}>{text}</span>
        </React.Fragment>
      )
    },
    {
      title: <FormattedMessage {...messages.status}/>,
      dataIndex: 'approval',
      render: (text, record) => (
        <React.Fragment>
          <Badge status={['success','processing','error'][text - 1]} />
          {[
            <FormattedMessage {...messages.registered}/>,
            <FormattedMessage {...messages.approvalPending}/>,
            <FormattedMessage {...messages.rejected}/>][text - 1]}
          {text === 2 && <div className={styles.statusWrap}>
            <Button size="small" onClick={() => this.onApprove(record.people_id, true)}>
              <FormattedMessage {...messages.approve}/>
            </Button>
            <Button size="small" type="danger" className={styles.reject} onClick={() => this.onApprove(record.people_id, false)}>
              <FormattedMessage {...messages.reject}/>
            </Button>
          </div>}
        </React.Fragment>
      ),
    },{
      title: <FormattedMessage {...messages.visitorNumber}/>,
      dataIndex: 'visitor_number',
      render: (text, record) => (
        <React.Fragment>
          <a href="javascript:;" onClick={() =>this.onOpenVisitorList(record)}>{text}</a>
        </React.Fragment>
      ),
    },
    // {
    //   title: '',
    //   dataIndex: 'equipment_number',
    //   render: (text, record) => (
    //     <React.Fragment>
    //       <a href="javascript:;" onClick={this.onOpenEquipmentList}>{text}</a>
    //     </React.Fragment>
    //   ),
    // },
    {
      title: <FormattedMessage {...messages.operate}/>,
      dataIndex: 'operate',
      render: (text, record) => (
      <div className={styles.operatorWrap}>
        {record.approval === 1 && <React.Fragment>
          
          
            <a href="javascript:;" className={styles.operatorLink} onClick={() =>this.showSelectModal(record.people_id)}>
              <FormattedMessage {...messages.relateDevice}/>
            </a>
            <Popconfirm
              title={<FormattedMessage {...messages.resetConfirm}/>}
              onConfirm={() => this.resetPwd(record.people_id)}
              className={styles.operatorLink}
            >
              <a href="javascript:;"><FormattedMessage {...messages.resetPassword}/></a>
            </Popconfirm>
            <a href="javascript:;" onClick={() => this.showEditModal(record)} className={styles.operatorLink} className={styles.operatorLink}>
            <FormattedMessage {...messages.edit}/>
            </a>
            <Popconfirm
              title={<FormattedMessage {...messages.deleteConfirm}/>}
              onConfirm={() => this.onDelete(record.people_id)}
              className={styles.operatorLink}
            >
              <a href="javascript:;"><FormattedMessage {...messages.delete}/></a>
            </Popconfirm>
          
        </React.Fragment>}
        {record.approval === 3 && <React.Fragment>
          <Popconfirm
              title={<FormattedMessage {...messages.deleteConfirm}/>}
              onConfirm={() => this.onDelete(record.people_id)}
              className={styles.operatorLink}
            >
              <a href="javascript:;"><FormattedMessage {...messages.delete}/></a>
            </Popconfirm>
        </React.Fragment>}
        </div>
      )
    }
  ];
  queryStrings = this.props.location.query || {}
  accountId = this.queryStrings.account_id || ''
  parentId = this.queryStrings.parent_id || ''

  state = {
    selectedRows: [],
    loading: false,
    confirmLoading: false,
    locked: false,
    visible: false,
    // 
    showView: false,
    // 
    showBatch: false,
    peoples: [],
    // 
    errPeoples: [],
    // 
    batchAddSuccess: 0,
    // 
    isShowImage: false,
    // url
    imageViewUrl: '',
    // 
    search_form: {},
    uuid: Date.now(),
    batchKey: Date.now() + Math.random(),
    total: 0,
    page_number: 1,
    page_size: defaultPageCount,
    // 
    viewListDataSource: [],
    isVisitorListVisible: false,
    isEquipmentListVisible: false,
    // 
    selectedPeopleId: '',
    batchDeleteMode: false,
  };

  componentDidMount() {
    const initAccountIds = this.getInitAccountIds();
    const form = this.props.form;
    const accountId =initAccountIds.slice(-1)[0]
    this.setState({
      search_form: {
        account_ids: initAccountIds,
        account_id: accountId,
      }
    });
    form.setFieldsValue({
      account_ids: initAccountIds,
      account_id: accountId,
    }, () => {
      this._loadAsyncData();
    })
  }

  onAccountChange = (value) => {
    const form = this.props.form
    form.setFieldsValue({
      account_id: value[value.length - 1]
    })
  }

  getInitAccountIds = () => {
    const userInfo = loadState('userInfo', true);
    const role = userInfo.role;
    const account_id = userInfo.account_id;
    let res = [account_id];
    // admin,accountId,admin/
    if (role === 1 && this.accountId) {
      if (this.parentId) {
        res.push(Number(this.parentId))
      }
      res.push(this.accountId)
    }
    return res
  }

  componentWillUnmount() {
    cancelApiRequsets();
  }

  _loadAsyncData() {
    this.setState({
      loading: true
    });

    let { page_number, page_size, search_form = {} } = this.state;
    let peoples = [];

    getPeoples({ page_number, page_size, ...search_form })
      .then(response => {
        let data = response.data;
        if (response.code === 1000) {
          peoples = data.peoples || [];
          let total = data.total_number || 0;
          this.setState({
            peoples,
            total,
            selectedRows: []

          });
        } else {
          this.setState({
            peoples: [],
            selectedRows: []
          });
          message.error(response.msg || this.props.intl.formatMessage(messages.failedToGetData));
        }
      })
      .catch(e => {
        console.log(e);
      })
      .finally(() => {
        this.setState({
          loading: false
        });
      });
  }

  toggleBatchDelete = (isDeleteMode) => {
    // 
    if (!isDeleteMode) {
      // 
      this.setState({
        selectedRows: []
      })
    }
    this.setState({
      batchDeleteMode: !this.state.batchDeleteMode
    })
  }

  /**
   * 
   * @param {Number} id
   */
  getInfo(id) {
    return new Promise((resolve, reject) => {
      getPeopleInfo(id).then((response) => {
        if (response.code === 1000) {
          resolve(response.data)
        } else {
          reject(response.msg || this.props.intl.formatMessage(messages.failedToGetData))
        }
      }).catch((e) => {
        reject(this.props.intl.formatMessage(messages.failedToGetData))
      })
    })
  }

  /**
   * 
   *
   * @memberof PersonnelManage
   */
  resetPwd = (id) => {
    resetPwd({
      people_id: id
    }).then((res) => {
      if (res.code === 1000) {
        message.success(this.props.intl.formatMessage(messages.resetSuccess))
      } else {
        message.error(res.msg || this.props.intl.formatMessage(messages.resetError))
      }
    }).catch(e => {
      message.error(this.props.intl.formatMessage(messages.resetError))
      console.log(e)
    })
  }


  /**
   * 
   */
  onBatchShow = () => {
    let batchFormData = {
      title: <FormattedMessage {...messages.batchAdd}/>,
    }
    this.setState({
      showBatch: true,
      batchFormData,
      batchKey: Date.now(),
    })
  }

  /**
   * 
   * @param {Number} id
   */
  onDelete = id => {
    deletePeople(id)
      .then(res => {
        if (res.code === 1000) {
          message.success(this.props.intl.formatMessage(messages.deleteSuccess));
          this.setState({
            selectedRows: this.state.selectedRows.filter((it) => it.people_id !== id)
          })
          this._loadAsyncData();
        } else {
          message.error(res.msg || this.props.intl.formatMessage(messages.deleteError));
        }
      })
      .catch(error => {
        // handle error
        message.error(this.props.intl.formatMessage(messages.deleteError));
      });
  };

  onApprove = (people_id, flag) => {
    approval({
      people_id,
      approval: flag,
    }).then((res) => {
      if (res.code === 1000) {
        message.success(this.props.intl.formatMessage(messages.operateSuccess))
        this._loadAsyncData()
      } else {
        message.error(this.props.intl.formatMessage(messages.operateError))
      }
    }).catch(e => {
      message.error(this.props.intl.formatMessage(messages.operateError))
      console.log(e)
    })
  }

  /**
   * 
   */
  onDeleteAll = () => {
    if (this.state.selectedRows) {
      const ids = this.state.selectedRows.reduce((res, item) => {
        res.push(item.people_id)
        return res
      }, [])
      batchDeletePeople(ids).then((res) => {
        if (res.code === 1000) {
          message.success(this.props.intl.formatMessage(messages.batchDeleteSuccess));
          this.setState({
            selectedRows: []
          })
          this._loadAsyncData();
        } else {
          message.error(res.msg || this.props.intl.formatMessage(messages.batchDeleteError));
        }
      })
      .catch(error => {
        // handle error
        message.error(this.props.intl.formatMessage(messages.batchDeleteError));
      })
    }
  }

  /**
   * 
   */
  handleStandardTableChange = (pagination, filtersArg, sorter) => {
    let { page_number, page_size } = this.state;

    if (page_size !== pagination.pageSize) {
      // page size changed, then reset page number to 1
      page_number = 1;
    } else {
      page_number = pagination.current;
    }

    page_size = pagination.pageSize;

    this.setState(
      {
        page_number,
        page_size
      },
      this._loadAsyncData
    );
  };

  /**
   * 
   */
  handleFormReset = () => {
    const { form } = this.props;
    form.resetFields();
    this.setState(
      {
        page_number: 1,
        search_form: {}
      },
      this._loadAsyncData
    );
  };

  /**
   * 
   * @param {Object} e 
   */
  handleSearch = e => {
    e.preventDefault();

    const { form } = this.props;

    form.validateFields((err, fieldsValue) => {
      if (err) return;

      this.setState(
        {
          page_number: 1,
          search_form: fieldsValue
        },
        this._loadAsyncData
      );
    });
  };

  /**
   * 
   */
  showAddForm = () => {
    let formData = {
      title: <FormattedMessage {...messages.addPeople}/>,
    };

    this.setState({ visible: true, formData });
  };

  /**
   * 
   */
  showEditModal = record => {
    if (this.state.locked) {
      return
    }
    this.setState({
      locked: true
    })
    this.getInfo(record.people_id).then((data) => {
      const people = data

      let formData = {
        title: <FormattedMessage {...messages.updatePeople}/>,
        ...people
      };
  
      this.setState({ visible: true, formData, locked: false });
    }).catch(err => {
      message.error(err);
      this.setState({
        locked: false,
      })
    })
  };

  /**
   * /
   * @param {Object} formRef /
   */
  saveFormRef = (formRef, key) => {
    this[key] = formRef;
  };

  

  /**
   * 
   * @param {Object} formRef 
   */
  saveBatchFormRef = formRef => {
    this.batchFormRef = formRef;
  };

  handleModal = (stateProps, flag) => {
    this.setState({
      [stateProps]: flag
    })
  }

  // onOpenEquipmentList = () => {
  //   let viewListDataSource = []
  //   this.setState({
  //     viewListDataSource,
  //   })
  //   this.handleModal('isEquipmentListVisible', true)  
  // }

  onOpenVisitorList = (record) => {
    let viewListDataSource = []
    getVisitorList({ page_number: 1, page_size: 9999, inviter_id: record.people_id})
    .then(response => {
      let data = response.data;
      if (response.code === 1000) {
        viewListDataSource = data.peoples || [];
        this.setState({
          viewListDataSource,
        });
        this.handleModal('isVisitorListVisible', true)
      } else {
        this.setState({
          viewListDataSource: []
        });
        message.error(response.msg || this.props.intl.formatMessage(messages.failedToGetData));
      }
    })
    .catch(e => {
      console.log(e);
      message.error(this.props.intl.formatMessage(messages.failedToGetData));
    })
  }

  /**
   * 
   * @param {String} url Url
   */
  showImage = (url) => {
    this.setState({
      isShowImage: true,
      imageViewUrl: url,
    })
  }

  /**
   * 
   */
  closeImage = () => {
    this.setState({
      isShowImage: false,
      imageViewUrl: '',
    })
  }

  doBatchAdd = async (selectedFileList, unSelectedFileList) => {
    const len = selectedFileList.length;
    this.setState({
      errPeoples: [],
      batchAddSuccess: 0,
    });
    for(let i =0; i<len; i++) {
      const formData = new FormData();
      const file = selectedFileList[i]
      formData.append('[]image', file.file);
      await batchAddPeople(formData).then((res) => {
        if (res.code === 1000) {
          this.setState({
            batchAddSuccess: this.state.batchAddSuccess + 1
          })
        } else if (res.code === 10301) {
          this.setState({
            errPeoples: [...this.state.errPeoples, ...res.data.err_people]
          })
        } else {
          console.log('error')
        }
      }).catch(e => {
        console.log('catch', e)
      })
    }
    // 
    if (this.state.errPeoples.length || unSelectedFileList.length) {
      let errDatas = groupBy(this.state.errPeoples, 'Errmsg')
      let errTypes = Object.keys(errDatas)
      const title = this.state.batchAddSuccess !== 0 ? 
        this.props.intl.formatMessage(messages.partFailError) :
        this.props.intl.formatMessage(messages.failError);
      Modal.info({
        title,
        content: (
          <React.Fragment>
            <div style={{maxHeight: '280px', overflowY: 'auto'}}>
            {unSelectedFileList.length ? <div><FormattedMessage {...messages.photoFormatError}/>:</div> : null}
            {unSelectedFileList.map((item) =>(<div key={item.name}>{item.name}</div>))}
            {errTypes.map((typemsg) => (<div key={typemsg}>
              <div>{typemsg}</div>
              <React.Fragment>{errDatas[typemsg].map((it) => (<div key={it.ImageFile}>{it.ImageFile}</div>))}</React.Fragment>
            </div>))}
            </div>
          </React.Fragment>
        ),
        onOk() {},
      });
      this.setState({
        showBatch: false,
        confirmLoading: false,
      },this.handleFormReset)
      // 
    } else if (this.state.batchAddSuccess === len) {
      message.success(this.props.intl.formatMessage(messages.batchAddSuccess))
      this.setState({
        showBatch: false,
        confirmLoading: false,
      },this.handleFormReset)
      // 
    } else {
      message.error(this.props.intl.formatMessage(messages.batchAddError))
      this.setState({
        confirmLoading: false,
      })
    }
  }

  /**
   * 
   */
  handleBatchCreate = () => {
    const form = this.batchFormRef.props.form;
    
    form.validateFields((err, values) => {
      if (err) {
        return;
      }
      const { imageList } = values;
      const { selectedFileList, unSelectedFileList } = imageList;
      // const formData = new FormData();

      if(!selectedFileList.length) {
        message.warning(this.props.intl.formatMessage(messages.photoFormatError));
        return;
      }

      this.setState({
        confirmLoading: true,
      })

      this.doBatchAdd(selectedFileList, unSelectedFileList);
    });
  };

      /**
   * 
   * @param {Object} record 
   */
  showViewModal = (record) => {
    if (this.state.locked) {
      return;
    }
    this.setState({
      locked: true
    })
    this.getInfo(record.people_id).then((data) => {
      const people = data

      let formData = {
        title: <FormattedMessage {...messages.peopleDetail}/>,
        ...people
      };
  
      this.setState({ showView: true, formData, locked: false });
    }).catch(err => {
      message.error(err);
      this.setState({
        locked: false,
      })
    })
  };


  /**
   * /
   */
  handleCreate = () => {
    const form = this.editFormRef.props.form;

    form.validateFields((err, values) => {
      if (err) {
        return;
      }
      const _image = values['image'] || {};
      const image = _image.file;
      const {people_id, people_name, telephone, idcard_number, mark } = values


      let fieldsValue = fixUndefinedData({
        image,
        people_id,
        people_name,
        telephone,
        idcard_number,
        mark,
      });

      const method = fieldsValue.people_id ? updatePeople: addPeople
      const msg = fieldsValue.people_id ? this.props.intl.formatMessage(messages.update) : this.props.intl.formatMessage(messages.add)
      this.setState({
        confirmLoading: true,
      });
      method(fieldsValue)
        .then(res => {
          if (res.code === 1000) {
            message.success(`${msg}${this.props.intl.formatMessage(messages.success)}`);
            if(fieldsValue.people_id) {
              this._loadAsyncData();
            } else {
              this.handleFormReset()
            }
            this.setState({
              visible: false,
            })
            form.resetFields();
          } else {
            message.error(res.msg || `${msg}${this.props.intl.formatMessage(messages.failed)}`);
          }
          this.setState({
            confirmLoading: false,
          });
        })
        .catch(error => {
          // handle error
          message.error(`${msg}${this.props.intl.formatMessage(messages.failed)}`);
          this.setState({
            confirmLoading: false,
          });
        })
    });
  };


  /**
   * 
   * @param {Array[Object]} 
   */
  handleSelectRows = rows => {
    this.setState({
      selectedRows: rows
    });
  };

  showSelectModal = (people_id) => {
    this.setState({
      selectedPeopleId: people_id,
      uuid: Date.now(),
    })
    this.handleModal('isSelectVisible', true)
  }


  renderSimpleForm() {
    const {
      form: { getFieldDecorator }
    } = this.props;

    return (
      <Form onSubmit={this.handleSearch} layout="inline">
        <Row gutter={{ md: 8, lg: 24, xl: 48 }}>
          {(loadState('userInfo', true) || {}).role !== 3 && <Col md={8} sm={24}>
          {getFieldDecorator('account_id')(<Input type="hidden"/>)}
          <FormItem label={<FormattedMessage {...messages.sourceAccount}/>}>
          {getFieldDecorator('account_ids')(
            <SelectAccount onChange={this.onAccountChange}/>
             )}
          </FormItem>
          </Col>}
          <Col md={8} sm={24}>
            <FormItem label={<FormattedMessage {...messages.personnelName}/>}>
              {getFieldDecorator('people_name')(
                <Input  maxLength={maxLength.userName}/>
              )}
            </FormItem>
          </Col>
          {/* <Col md={8} sm={24}>
            <FormItem label={<FormattedMessage {...messages.idcardNumber}/>}>
              {getFieldDecorator('idcard_number')(
                <Input  maxLength={maxLength.idcardNumber}/>
              )}
            </FormItem>
          </Col> */}
          <Col md={8} sm={24}>
            <span className={styles.submitButtons}>
              <Button type="primary" htmlType="submit">
              <FormattedMessage {...messages.search}/>
              </Button>
              <Button style={{ marginLeft: 8 }} onClick={this.handleFormReset}>
              <FormattedMessage {...messages.reset}/>
              </Button>
            </span>
          </Col>
        </Row>
      </Form>
    );
  }

  render() {
    const {
      loading,
      selectedRows,
      peoples,
      total,
      visible,
      formData,
      batchFormData,
      page_number,
      page_size,
      confirmLoading,
    } = this.state;

    const pagination = {
      current: page_number,
      pageSize: page_size,
      total,
    };

    return (
      <React.Fragment>

        <Card bordered={false}>
          <div className={styles.tableList}>
            <div className={styles.tableListForm}>
              {this.renderSimpleForm()}
            </div>
            <ListHeader
              title={<FormattedMessage {...messages.peopleList}/>}
              customButton={
                <React.Fragment>
                  {this.state.batchDeleteMode ? <React.Fragment>
                    <Button onClick={() => this.toggleBatchDelete(false)} style={{marginRight: 5}}>
                      <FormattedMessage {...messages.cancel}/>
                    </Button>
                    {selectedRows.length <= 0 ? 
                     <Button disabled><FormattedMessage {...messages.confirmDelete}/></Button>:
                     <Popconfirm
                        title={<FormattedMessage {...messages.batchDeleteConfirm}/>}
                        onConfirm={() => this.onDeleteAll()}>
                         <Button><FormattedMessage {...messages.confirmDelete}/></Button>
                      </Popconfirm>
                    }
                  </React.Fragment> : <React.Fragment>
                    <Button type="default" onClick={this.showAddForm} style={{marginRight: 5}}>
                      <FormattedMessage {...messages.addPeople}/>
                    </Button>
                    <Button type="default" onClick={this.onBatchShow} style={{marginRight: 5}}>
                      <FormattedMessage {...messages.batchAdd}/>
                    </Button>
                    <Button onClick={() =>this.toggleBatchDelete(true)} >
                      <FormattedMessage {...messages.batchDelete}/>
                    </Button>
                  </React.Fragment>}
                </React.Fragment>
              }
              style={{ marginBottom: 5 }}
          />

            <StandardTable
              rowKey="people_id"
              selectedRows={selectedRows}
              selectable={this.state.batchDeleteMode}
              showAlert={false}
              pagination={pagination}
              loading={loading}
              dataList={peoples}
              columns={this.columns}
              onSelectRow={this.handleSelectRows}
              onChange={this.handleStandardTableChange}
            />
          </div>
        </Card>

        <EditForm
          wrappedComponentRef={(formRef) => this.saveFormRef(formRef, 'editFormRef')}
          visible={visible}
          onCancel={() => this.handleModal('visible', false)}
          onCreate={this.handleCreate}
          formData={formData}
          confirmLoading={confirmLoading}
        />
        
        <BatchForm
          wrappedComponentRef={this.saveBatchFormRef}
          visible={this.state.showBatch}
          onCancel={() => this.handleModal('showBatch', false)}
          onCreate={this.handleBatchCreate}
          formData={this.state.batchFormData}
          key={this.state.batchKey}
          confirmLoading={confirmLoading}
        />

       

        <ImageViewer imageUrl={this.state.imageViewUrl} isOpen={this.state.isShowImage} onClose={this.closeImage}></ImageViewer>

          <VisitorList  
            visible={this.state.isVisitorListVisible}
            onCancel={() => this.handleModal('isVisitorListVisible', false)}
            dataList={this.state.viewListDataSource} />

          {/* <EquipmentList
            visible={this.state.isEquipmentListVisible}
            onCancel={() => this.handleModal('isEquipmentListVisible', false)}
            dataList={this.state.viewListDataSource} /> */}


          <BindEquipment 
            visible={this.state.isSelectVisible}
            onCancel={() => this.handleModal('isSelectVisible', false)}
            uuid={this.state.uuid}
            peopleId={this.state.selectedPeopleId}
          />

          <ViewForm
            visible={this.state.showView}
            onCancel={() => this.handleModal('showView', false)}
            formData={this.state.formData}
          /> 

      </React.Fragment>
    );
  }
}

export default injectIntl(Form.create()(PersonnelManage));
