import React from 'react';
import { Modal } from 'antd';

import PhotoBox from '../../../components/PhotoBox';
import UFormRow from '../../../components/UFormRow'

import { injectIntl, FormattedMessage, defineMessages } from 'react-intl';


const messages = defineMessages({
  personnelName: {
    id: 'personnel.name',
    defaultMessage: '',
  },
  mobilePhone: {
    id: 'personnel.mobilePhone',
    defaultMessage: '',
  },
  photo: {
    id: 'personnel.photo',
    defaultMessage: '',
  },
  mark: {
    id: 'common.mark',
    defaultMessage: '',
  },
  idcardNumber: {
    id: 'personnel.idcardNumber',
    defaultMessage: '',
  },
})

class ViewForm extends React.Component {
    state = {}

    render() {
      const {
        visible,
        onCancel,
        formData,
      } = this.props;
      
      const data = formData || {};

      const list = [
        {
          title: `${this.props.intl.formatMessage(messages.personnelName)}`,
          content: data.people_name,
        },
        {
          title: `${this.props.intl.formatMessage(messages.photo)}`,
          content: <PhotoBox photo={data.thumbnail} width={130} height={130}/>
        },
        // {
        //   title: `${this.props.intl.formatMessage(messages.mobilePhone)}`,
        //   content: data.telephone,
        // },
        // {
        //   title: `${this.props.intl.formatMessage(messages.idcardNumber)}`,
        //   content: data.idcard_number,
        // },
        ];

      return (
        <Modal
          visible={visible}
          title={data.title}
          onCancel={onCancel}
          width={400}
          footer={null}
          destroyOnClose={true}
        >
        <div>    
          {list.map(it => (
            <UFormRow 
              labelSpan={6} 
              title={it.title} 
              content={it.content} 
              key={it.title} 
              labelPosition="right"/>
          ))}
        </div>
        </Modal>
      );
    }
  }

export default injectIntl(ViewForm);
