import React from 'react';
import { injectIntl, FormattedMessage, defineMessages } from 'react-intl';
import { Modal, message, Tabs, Table,} from 'antd';
import ListHeader from '../ListHeader'

const TabPane = Tabs.TabPane;

const messages = defineMessages({
  failedToGetData: {
    id: 'common.error.failedToGetData',
    defaultMessage: '!',
  },
  selectedRowRequired: {
    id: 'common.selectedRow.required',
    defaultMessage: '!'
  }
});


class TabTableModal extends React.Component {

  state = {
    selectedRows: [],
    selectedRowKeys: [],
    loading: false,
    list: [],
    selectedList: [],
    total: 0,
    page_number: 1,
    page_size: 10,
    search_form: {},
    tabParams:{},
    formKey: Date.now(),
  }

  /**
   *tab
   *
   * @memberof TabTableModal
   */
  componentDidMount() {
    const tabParams = this.props.tabParams
    if (tabParams.length) {
      this.setState({
        tabParams: tabParams[0]
      }, this._loadAsyncData)
    } else {
      this._loadAsyncData()
    }
  }

  // 
  _loadAsyncData() {
    const params = this.props.requestParams
    const initParams = this.props.initParams || {}
    const responseKey = this.props.responseKey
    let { page_number, page_size, search_form = {}, tabParams } = this.state;
    let combineData = {page_number, page_size, ...initParams, ...search_form, ...params, ...tabParams}
    let requiredKey = this.props.requiredParamKeyName
    if (requiredKey && !combineData[requiredKey]) {
      console.log('lack of required param!')
      return
    }
    this.setState({
      loading: true
    });
    // params 
    // search_form 
    // tabParams tab
    this.props.requestMethod(combineData).then(res => {
      const data = res.data
      if (res.code === 1000) {
        let total = data.total_number || 0;
        this.setState({
          list: data[responseKey],
          total,
        })
      } else {
        message.error(res.msg || this.props.intl.formatMessage(messages.failedToGetData))
      }
    }).catch(err => {
      message.error(this.props.intl.formatMessage(messages.failedToGetData))
    }).finally(() => {
      this.setState({
        loading: false
      });
    })
  }

  // 
  handleRowSelectChange = (selectedRowKeys, selectedRows) => {
    this.setState({ selectedRowKeys, selectedRows});
  };


  onSearch = (e)=> {
    e.preventDefault();
    const form = this.formRef.props.form;
    form.validateFields((err, values) => {
      if (err) {
          return;
      }
      this.setState({
        page_number: 1,
        search_form: values,
      }, this._loadAsyncData)
    });
  }

  // 
  handleStandardTableChange = (pagination, filtersArg, sorter) => {
    let { page_number, page_size } = this.state;

    if (page_size !== pagination.pageSize) {
      // page size changed, then reset page number to 1
      page_number = 1;
    } else {
      page_number = pagination.current;
    }

    page_size = pagination.pageSize;

    this.setState(
      {
        page_number,
        page_size
      },
      this._loadAsyncData
    );
  };

  saveFormRef = (formRef) => {
    this.formRef = formRef;
  }

  // tab
  onTabChange = (k) => {
    // key'1''2'
    const currIndex = k -1
    const tabParams = this.props.tabParams
    if (tabParams.length) {
      let p = tabParams[currIndex]
      this.setState({
        tabParams: p,
        selectedRowKeys: [],
        selectedRows: [],
        page_number: 1,
        search_form: [],
        formKey: Date.now(),
      }, this._loadAsyncData)
    }
  }

  // tab
  operateOne = () => {
    if (!this.state.selectedRowKeys.length) {
      message.warning(this.props.intl.formatMessage(messages.selectedRowRequired))
      return
    }
    const callback = (function() {
      this.setState({
        selectedRowKeys: [],
        selectedRows: [],
      }, this._loadAsyncData)
    }).bind(this)
    this.props.operateActions[0](this.state.selectedRowKeys, callback)
  }

  // tab
  operateTwo = () => {
    if (!this.state.selectedRowKeys.length) {
      message.warning(this.props.intl.formatMessage(messages.selectedRowRequired))
      return
    }
    const callback = (function() {
      this.setState({
        selectedRowKeys: [],
        selectedRows: [],
      }, this._loadAsyncData)
    }).bind(this)
    this.props.operateActions[1](this.state.selectedRowKeys, callback)
  }

  render() {
    const {
      visible,
      onCancel,
      title,
      columns,
      rowKey,
      confirmLoading,
      formComponent: FormComponent,
      totalLabel,
      tabTitles = ['Tab1','Tab2'],
      operateLabels = [],
    } = this.props;

    const hasSearch = !!FormComponent
    const {
      loading,
      selectedRowKeys,
      total,
      list,
      page_number,
      page_size,
      formKey,
    } = this.state;

    const pagination = {
      current: page_number,
      pageSize: page_size,
      total,
      showQuickJumper: true,
    };

    const rowSelection = {
      onChange: this.handleRowSelectChange,
      selectedRowKeys: selectedRowKeys,
      getCheckboxProps: record => ({
        disabled: record.disabled,
      }),
    };
    
    return (
      <Modal
        visible={visible}
        title={title}
        onCancel={onCancel}
        confirmLoading={confirmLoading}
        width={920}
        destroyOnClose={true}
        footer={null}
      >
        <Tabs defaultActiveKey="1" onChange={this.onTabChange} animated={false}>
          <TabPane tab={tabTitles[0]} key="1">
            {hasSearch && <div style={{height: '60px'}}>
              <FormComponent handleSearch={this.onSearch} wrappedComponentRef={this.saveFormRef} key={formKey}/>
            </div>}

            <ListHeader
              title={`${totalLabel}${this.state.total}`}
              buttonText={operateLabels[0]}
              buttonType="default"
              doAction={this.operateOne}
            />

          </TabPane>
          <TabPane tab={tabTitles[1]} key="2">
          {hasSearch && <div style={{height: '60px'}}>
              <FormComponent handleSearch={this.onSearch} wrappedComponentRef={this.saveFormRef} key={formKey}/>
            </div>}
            <ListHeader
              title=""
              buttonText={operateLabels[1]}
              buttonType="default"
              doAction={this.operateTwo}
            />

          </TabPane>
        </Tabs>
        <Table
                rowKey={rowKey}
                loading={loading}
                columns={columns}
                dataSource={list}
                onChange={this.handleStandardTableChange}
                rowSelection={rowSelection}
                pagination={pagination}
                scroll={{ y: hasSearch ? 340 : 400 }}
              />



      </Modal>
    );
  }
}

export default injectIntl(TabTableModal);
