import React from 'react';
import { Modal, Form, Input, DatePicker, Tooltip,Icon } from 'antd';
import { injectIntl, FormattedMessage, defineMessages } from 'react-intl';
import Avatar from '../../../components/Avatar';

import { PHONE_REG, LETTER_NUM_REG } from '../../../utils/utils';
import maxLength from '../../../utils/maxLength'


const FormItem = Form.Item;
const { RangePicker } = DatePicker;

const messages = defineMessages({
  personnelName: {
    id: 'personnel.name',
    defaultMessage: '',
  },
  photo: {
    id: 'personnel.photo',
    defaultMessage: '',
  },
  mobilePhone: {
    id: 'personnel.mobilePhone',
    defaultMessage: ''
  },
  mobilePhoneFormat: {
    id: 'personnel.mobilePhone.format',
    defaultMessage: '6-20!'
  },
  personnelNameRequired: {
    id: 'personnel.name.required',
    defaultMessage: '!'
  },
  photoRequired: {
    id: 'personnel.photo.required',
    defaultMessage: '!'
  },
  mark: {
    id: 'common.mark',
    defaultMessage: '',
  },
  idcardNumber: {
    id: 'personnel.idcardNumber',
    defaultMessage: '',
  },
});

const EditForm = Form.create()(
  class extends React.Component {

    render() {
      const { visible, onCancel, onCreate, form, formData, confirmLoading } = this.props;
      const { getFieldDecorator } = form;
      const locale = this.props.intl.locale;
      const formItemLayout = {
        labelCol: {
          xs: { span: 24 },
          sm: { span: locale === 'en' ? 7 : 4 }
        },
        wrapperCol: {
          xs: { span: 24 },
          sm: { span: locale === 'en' ? 17 : 18 }
        }
      };
      let model = formData || {};
      


      return (
        <Modal
          visible={visible}
          title={model.title}
          onCancel={onCancel}
          onOk={onCreate}
          width={420}
          destroyOnClose={true}
          confirmLoading={confirmLoading}
        >
          <Form>
            {getFieldDecorator('people_id', {
              initialValue: model.people_id
            })(<Input type="hidden" />)}
            <FormItem label={<FormattedMessage {...messages.personnelName} />} {...formItemLayout}>
              {getFieldDecorator('people_name', {
                rules: [
                  {
                    required: true,
                    message: <FormattedMessage {...messages.personnelNameRequired} />
                  }
                ],
                initialValue: model.people_name
              })(<Input maxLength={maxLength.userName}/>)}
            </FormItem>
            <FormItem {...formItemLayout} label={<FormattedMessage {...messages.photo} />}>
              {getFieldDecorator('image', {
                rules: [
                  {
                    required: true,
                    message: <FormattedMessage {...messages.photoRequired} />
                  }
                ],
                initialValue: model.image && { photo_url: model.image }
              })(<Avatar/>)}
            </FormItem>
            {/* <FormItem {...formItemLayout} label={<FormattedMessage {...messages.mobilePhone} />}>
                {getFieldDecorator('telephone', {
                  rules: [
                    {
                      pattern:  PHONE_REG,
                      message: <FormattedMessage {...messages.mobilePhoneFormat} />
                    }
                  ],
                  initialValue: model.telephone
                })(<Input />)}
              </FormItem>
              <FormItem label={<FormattedMessage {...messages.idcardNumber} />} {...formItemLayout}>
              {getFieldDecorator('idcard_number', {
                initialValue: model.idcard_number
              })(<Input maxLength={maxLength.idcardNumber}/>)}
            </FormItem>
            <FormItem label={<FormattedMessage {...messages.mark} />} {...formItemLayout}>
              {getFieldDecorator('mark', {
                initialValue: model.mark
              })(<Input maxLength={maxLength.mark}/>)}
            </FormItem> */}
          </Form>
        </Modal>
      );
    }
  }
);

export default injectIntl(EditForm);
