import React, {Component} from 'react'
import TabTableModal from '../../../../components/TabTableModal'
import { injectIntl, FormattedMessage, defineMessages } from 'react-intl';
import { loadState } from '../../../../utils/localStorage';

import {
  Badge,
} from 'antd';


import {
  binding,
  unbinding,
  getBoundEquipmentList
} from '../../../../api/binding'
import { message } from 'antd';

import Search from './Search'

const wordBreak = {wordBreak:'break-all', wordWrap:'break-word'}

const messages = defineMessages({
  deviceName: {
    id: 'device.deviceName',
    defaultMessage: ''
  },
  deviceID: {
    id: 'device.deviceID',
    defaultMessage: 'ID'
  },
  connectStatus: {
    id: 'device.connectStatus',
    defaultMessage: ''
  },
  sourceAccount: {
    id: 'account.sourceAccount',
    defaultMessage: ''
  },
  relateSuccess: {
    id: 'common.relate.success',
    defaultMessage: '!'
  },
  relateError: {
    id: 'common.relate.error',
    defaultMessage: '!'
  },
  cancelRelateSuccess: {
    id: 'common.cancelRelate.success',
    defaultMessage: '!'
  },
  cancelRelateError: {
    id: 'common.cancelRelate.error',
    defaultMessage: '!'
  },
  relateDevice: {
    id: 'device.relateDevice',
    defaultMessage: ''
  },
  related: {
    id: 'common.related',
    defaultMessage: ''
  },
  unrelated: {
    id: 'common.unrelated',
    defaultMessage: ''
  },
  relate: {
    id: 'common.relate',
    defaultMessage: ''
  },
  cancelRelate: {
    id: 'common.cancelRelate',
    defaultMessage: ''
  },
  relatedDeviceNumber: {
    id: 'device.relatedDeviceNumber',
    defaultMessage: ''
  },
  online: {
    id: 'common.online',
    defaultMessage: ''
  },
  offline: {
    id: 'common.offline',
    defaultMessage: ''
  },
})

const columns = [
  {
    title: <FormattedMessage {...messages.deviceName}/>,
    dataIndex: 'equipment_name',
    width: 100,
    render: (text, record) =>
    <React.Fragment>
      <div style={wordBreak}>{text}</div>
    </React.Fragment>
  },
  // ID
  {
    title: <FormattedMessage {...messages.deviceID}/>,
    dataIndex: 'equipment_id',
    width: 100,
  },
  {
    title: <FormattedMessage {...messages.connectStatus}/>,
    dataIndex: 'status',
    width: 100,
    render: (text,record) => (
      <React.Fragment>
        <Badge status={['success','error'][text - 1]} />
        {[<FormattedMessage {...messages.online} />, <FormattedMessage {...messages.offline} />][text-1]}
      </React.Fragment>
    )
  },
  {
    title: <FormattedMessage {...messages.sourceAccount}/>,
    dataIndex: 'account_name',
    width: 100,
    render: (text, record) =>
    <React.Fragment>
      <div style={wordBreak}>{text}</div>
    </React.Fragment>
  },
]

class BindEquipment extends Component {
  // 
  binding = (data, callback) => {
    binding({
      people_ids: [this.props.peopleId],
      equipment_ids: data
    }).then((res) => {
      if (res.code === 1000) {
        message.success(this.props.intl.formatMessage(messages.relateSuccess))
        // 
        callback && callback()
      } else {
        message.error(res.msg || this.props.intl.formatMessage(messages.relateError))
      }
    }).catch(e => {
      console.log(e)
    })
  }

  // 
  unbinding = (data, callback) => {
    unbinding({
      people_ids: [this.props.peopleId],
      equipment_ids: data
    }).then((res) => {
      if (res.code === 1000) {
        message.success(this.props.intl.formatMessage(messages.cancelRelateSuccess))
        // 
        callback && callback()
      } else {
        message.error(res.msg || this.props.intl.formatMessage(messages.cancelRelateError))
      }
    }).catch(e => {
      console.log(e)
    })
  }

  render() {
    const {
      visible,
      onCancel,
      uuid,
      peopleId
    } = this.props

    const userInfo = loadState('userInfo', true);
    const role = userInfo.role;
    const account_id = userInfo.account_id;
    const initParams = {
      account_id: account_id,
      account_ids: [account_id],
    }

    return (
      <TabTableModal
        title={<FormattedMessage {...messages.relateDevice}/>}
        tabTitles={[<FormattedMessage {...messages.related}/>,<FormattedMessage {...messages.unrelated}/>]}
        tabParams={[{binding: 1},{binding: 0}]}
        visible={visible} 
        onCancel={onCancel}
        columns={columns}
        key={uuid}
        responseKey="equipments"
        rowKey="equipment_id"
        totalLabel={`${this.props.intl.formatMessage(messages.relatedDeviceNumber)}`}
        operateLabels={[<FormattedMessage {...messages.cancelRelate}/>,<FormattedMessage {...messages.relate}/>]}
        operateActions={[this.unbinding, this.binding]}
        requestMethod={getBoundEquipmentList}
        requestParams={{people_id: peopleId}}
        initParams = {initParams}
        requiredParamKeyName="people_id"
        formComponent={Search}/>
    )
  }
}

export default injectIntl(BindEquipment)