import React, {Component} from 'react';
import { Modal, Table,Badge } from 'antd';

import ViewList from '../../../components/ViewList'
import PhotoBox from '../../../components/PhotoBox'
import { injectIntl, FormattedMessage, defineMessages } from 'react-intl';

import {parseDate} from '../../../utils/utils';

const wordBreak = {wordBreak:'break-all', wordWrap:'break-word'}

const messages = defineMessages({
  peopleName: {
    id: 'personnel.name',
    defaultMessage: '',
  },
  photo: {
    id: 'personnel.photo',
    defaultMessage: '',
  },
  mobilePhone: {
    id: 'personnel.mobilePhone',
    defaultMessage: '',
  },
  visitReason: {
    id: 'personnel.visitReason',
    defaultMessage: '',
  },
  visitTime: {
    id: 'personnel.visitTime',
    defaultMessage: '',
  },
  inviteTime: {
    id: 'personnel.inviteTime',
    defaultMessage: '',
  },
  status: {
    id: 'common.status',
    defaultMessage: '',
  },
  accepted: {
    id:'common.accepted',
    defaultMessage: ''
  },
  confirmPending: {
    id: 'common.confirmPending',
    defaultMessage: ''
  },
  rejected: {
    id: 'common.rejected',
    defaultMessage: ''
  },
  visitorList: {
    id: 'personnel.visitorList',
    defaultMessage: '',
  }
})

const columns = [
  {
    title: <FormattedMessage {...messages.peopleName}/>,
    dataIndex: 'people_name',
    width: 100,
    render: (text, record) =>
    <React.Fragment>
      <div style={wordBreak}>{text}</div>
    </React.Fragment>
  },
  {
    title: <FormattedMessage {...messages.photo} />,
    dataIndex: 'image',
    width: 100,
    render: (text, record) =>
    <React.Fragment>
      {record.image && <PhotoBox photo={record.thumbnail} width={50} height={50}/>}
    </React.Fragment>
  },
  {
    title: <FormattedMessage {...messages.mobilePhone} />,
    dataIndex: 'telephone',
    width: 100,
  },
  {
    title: <FormattedMessage {...messages.visitReason}/>,
    dataIndex: 'reasons_for_access',
    width: 100,
    render: (text, record) =>
    <React.Fragment>
      <div style={wordBreak}>{text}</div>
    </React.Fragment>
  },
  {
    title: <FormattedMessage {...messages.visitTime}/>,
    dataIndex: 'visit_time',
    width: 148,
    render: (text, record) =>
    <React.Fragment>
      {parseDate(record.start_time)} - {parseDate(record.end_time)}
    </React.Fragment>
  },
  {
    title: <FormattedMessage {...messages.inviteTime}/>,
    dataIndex: 'inviter_time',
    width: 100,
    render: (text, record) =>
    <React.Fragment>
      {parseDate(text)}
    </React.Fragment>
  },
  {
    title: <FormattedMessage {...messages.status}/>,
    width: 80,
    dataIndex: 'inviter_status',
    render: (text, record) =>
    <React.Fragment>
      <Badge status={['success','processing','error'][text - 1]} />
      {[
        <FormattedMessage {...messages.accepted}/>,
        <FormattedMessage {...messages.confirmPending}/>,
        <FormattedMessage {...messages.rejected}/>
      ][text-1]}
    </React.Fragment>
  },
]

class VisitorList extends Component {
  state = {
    title: <FormattedMessage {...messages.visitorList}/>,
    rowKey: 'people_id'
  }
  render() {
    const { visible, onCancel, dataList } = this.props
    const {title, rowKey} = this.state

    return (
      <ViewList 
        visible={visible}
        onCancel={onCancel}
        title={title}
        rowKey={rowKey}
        columns={columns}
        dataList={dataList}
       />
    )
  }
}

export default injectIntl(VisitorList)