import React from 'react';
import { Col, Row } from 'antd';

import styles from './index.module.scss';

class UFormRow extends React.Component {
  render() {
    const {labelSpan, title, content, data, labelPosition,style} = this.props

    const contentSpan = {
      top: 24,
      right: 24-labelSpan,
      left: ''
    }[labelPosition]

    return (
      <Row className={styles.formItem} style={{...style}}>
        <Col span={labelSpan} className={styles[labelPosition]}>{data.title || title}</Col>
        <Col span={contentSpan}>{data.content || content}</Col>
      </Row>
    )
  }
}

UFormRow.defaultProps = {
  data: {},
  labelSpan: 8,
  title: '',
  content: '',
  labelPosition: 'left'
}

export default UFormRow