import fetch from '../utils/fetch'

const apis = {
  binding: '/api/binding',
  equipment: '/api/binding/equipments',
  people: '/api/binding/peoples',
  unbinding: '/api/binding/untying',
}

export function binding(data) {
  return fetch.post(apis.binding, data)
}

export function getBoundEquipmentList(data) {
  return fetch.get(apis.equipment, {
    params: data
  })
}

export function getBoundPeopleList(data) {
  return fetch.get(apis.people, {
    params: data
  })
}

export function unbinding(data) {
  return fetch.post(apis.unbinding, data)
}