import React, {Component} from 'react';
import { Modal, Form, Input, DatePicker, Select,Tooltip,Icon, Upload, Button ,message, Card, Spin} from 'antd';
import { injectIntl, FormattedMessage, defineMessages } from 'react-intl';
import uploadFix, { dataURLtoFile, getOrientation, getBase64 } from '../../utils/uploadFix';

const messages = defineMessages({
  selected: {
    id: 'common.selected',
    defaultMessage: ''
  },
  selectFiles: {
    id: 'personnel.selectFiles',
    defaultMessage: '',
  },
  compress: {
    id: 'common.compress',
    defaultMessage: '...',
  },
});

let oriFileList = []
let oriFileLen = 0

class BatchUpload extends Component {
  
  state = {
    fileList: [],
    unSelectedFileList: [],
    selectedFileList: [],
    uploading: false,
  };

  onCustomRequest = (obj) => {
    const file = obj.file;
    // 
    getOrientation(file).then((orientation) => {
      const reader = new FileReader();
      const img = new Image();
      // iosorientation1
      const needRotate = (orientation && orientation !== 1)
      reader.onload = (e) => {
        if (needRotate) {
          img.src = e.target.result;
          img.onload = () => {
            // 
            const data = uploadFix.compress(img, orientation);
            // base64tofile
            const newFile = dataURLtoFile(data, file.name);
            const fileInfo = {
              photo_url: data,
              file: newFile
            };
            this.updateSelected(fileInfo);
          };            
        } else {
          const fileInfo = {
            photo_url:  e.target.result,
            file,
          }
          this.updateSelected(fileInfo);
        }
      };
      reader.readAsDataURL(file);
    });
    return false;
  };
  updateSelected = (fileInfo) => {
    const selected = [...this.state.selectedFileList, fileInfo]
    const maxLen = this.state.fileList.length
    this.setState({
      selectedFileList: selected,
      uploading: selected.length < maxLen,
    });
    this.triggerChange({
      selectedFileList: selected,
      unSelectedFileList: this.state.unSelectedFileList,
    });
  };
  triggerChange = (files)  => {
    const onChange = this.props.onChange;
    const {uploading} = this.state;
    if (onChange) {
      onChange(Object.assign({}, files, { uploading }));
    }
  };
  onBeforeUpload = (file, fileList) => {
    if (!oriFileLen) {
      oriFileList = fileList
      oriFileLen = oriFileList.length
      this.setState(({ fileList }) => ({
        fileList: [],
        unSelectedFileList: [],
        selectedFileList: [],
        uploading: false,
      }));
    }
    oriFileLen = oriFileLen - 1;
    const isNotImg = file.type !== 'image/jpeg' && file.type !== 'image/png' && file.type !== 'image/jpg';
    if (isNotImg) {
      console.log('JPEG/JPG/PNG!');
      this.setState(({ unSelectedFileList }) => ({
        unSelectedFileList: [...(unSelectedFileList || []), file],
      }), () => {
        this.triggerChange({
          selectedFileList: this.state.selectedFileList,
          unSelectedFileList: this.state.unSelectedFileList,
        });
      });
    }

    const isLt2M = file.size / 1024 / 1024  < 2;

    if (!isLt2M) {
      console.log('2M');
      this.setState(({ unSelectedFileList }) => ({
        unSelectedFileList: [...(unSelectedFileList || []), file],
      }), () => {
        this.triggerChange({
          selectedFileList: this.state.selectedFileList,
          unSelectedFileList: this.state.unSelectedFileList,
        });
      });
    }
    const res = (!isNotImg) && isLt2M
    if (res) {
      this.setState(({ fileList }) => ({
        fileList: [...(fileList || []), file],
      }));
    }
    return res
  };

  render() {
    const {children, showSelected, selectText} = this.props
    const uploading = this.state.selectedFileList.length < this.state.fileList.length
    return(
      <div>
        <Upload
          disabled={uploading}
          name="upload"
          action="" 
          accept="image/jpg, image/jpeg, image/png" 
          multiple 
          showUploadList={false}
          customRequest={this.onCustomRequest}
          beforeUpload={this.onBeforeUpload}>
            { children ? children : <Button style={{paddingLeft: '20px', paddingRight: '20px'}}>
              <Icon type="upload" />{ selectText ? selectText : <FormattedMessage {...messages.selectFiles}/>}
            </Button>
            }
        </Upload>
       {showSelected && <React.Fragment>
          &nbsp;(<FormattedMessage {...messages.selected}/>&nbsp;{this.state.fileList.length + this.state.unSelectedFileList.length})
       </React.Fragment>}
       {uploading && <React.Fragment>&nbsp;<Spin size="small"/><FormattedMessage {...messages.compress}/></React.Fragment>}{uploading}
       
      </div>
    )
  }
}

BatchUpload.defaultProps = {
  showSelected: true,
}

export default injectIntl(BatchUpload)