import React from 'react';
import { Modal, Form, Input, DatePicker, Select,Tooltip,Icon, Upload, Button ,message, Card} from 'antd';
import { injectIntl, FormattedMessage, defineMessages } from 'react-intl';
import Avatar from '../Avatar';
import BatchUpload from './BatchUpload'

import { PHONE_REG, IDCARD_REG, LETTER_NUM_REG } from '../../utils/utils'

import styles from './index.module.scss';

const messages = defineMessages({
  photo: {
    id: 'personnel.photo',
    defaultMessage: '',
  },
  photoFormat: {
    id: 'personnel.photo.format',
    defaultMessage: 'JPG/JPEG/PNG2M+&/"+"/"&"'
  },
  photoRequired: {
    id: 'personnel.photo.required',
    defaultMessage: '!'
  },
  selectFiles: {
    id: 'personnel.selectFiles',
    defaultMessage: '',
  }
});

const FormItem = Form.Item;
const Option = Select.Option;
const { RangePicker } = DatePicker;

const formItemLayout = {
  labelCol: {
    xs: { span: 24 },
    sm: { span: 5 }
  },
  wrapperCol: {
    xs: { span: 24 },
    sm: { span: 18 }
  }
};

const batchForm = Form.create()(
  class extends React.Component {
    state = {
      uploading: false,
    };
    onOk = () => {
      if (this.state.uploading) {
        return false;
      }
      this.props.onCreate();
    };
    onImageChange = (v) => {
      this.setState({
        uploading: v.uploading,
      });
    };
    render() {
      const { visible, onCancel, onCreate, form, formData, confirmLoading } = this.props;
      const { getFieldDecorator } = form;

      let model = formData || {};
    
      
      return (
        <Modal
          visible={visible}
          title={model.title}
          onCancel={onCancel}
          onOk={this.onOk}
          confirmLoading={confirmLoading}
          width={500}
          destroyOnClose={true}
        >
          <Form>
              <FormItem {...formItemLayout} label={<span><Tooltip title={<FormattedMessage {...messages.photoFormat}/>}>
                <Icon type="question-circle-o" />
              </Tooltip> <FormattedMessage {...messages.photo}/></span>}>
              {getFieldDecorator('imageList', {
                rules: [
                  {
                    required: true,
                    message: <FormattedMessage {...messages.photoRequired}/>
                  }
                ]
              })(
                <BatchUpload onChange={this.onImageChange}/>
              )}
              </FormItem>
              </Form>
        </Modal>
      );
    }
  }
);

export default batchForm;
