import React from 'react';
import { Modal, Table } from 'antd';


class ViewList extends React.Component {


  render() {
    const {
      visible,
      onCancel,
      title,
      columns = [],
      rowKey,
      dataList = []
    } = this.props;



    return (
      <Modal
        title={title}
        onCancel={onCancel}
        width={920}
        visible={visible}
        footer={null}
        destroyOnClose={true}
      >
        <Table
          rowKey={rowKey}
          columns={columns}
          dataSource={dataList}
          onChange={this.handleStandardTableChange}
          pagination={false}
          scroll={{ y: 450 }}
            />
      </Modal>
    );
  }
}

export default ViewList;
